.wrapper {
  overflow: hidden;
  min-height: 100vh;
  position: relative;
  display: flex;
  flex-direction: column;
}

body {
//   font-family: 'PT Serif', serif;
//   @include Text-Style-25;
//   background:rgb(225, 228, 250);
background-color: $white;
}


.container {
  // outline: 1px solid #4164ff;
  max-width: 1160px;
  width: 100%;
  margin: 0 auto;
  position: relative;
}

.inner {
  position: relative;
}
.svgSpriteWrap{
  position: absolute;
}
a {
  display: inline-block;
  @include transition;
  cursor: pointer;
  color: $jungle-green;
  &:hover{
    color: lighten($jungle-green, 25%);
  }
}

.imgs {
  overflow: hidden;
  position: relative;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.imgs img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: top;
  position: absolute;
  top: 0;
  left: 0;
}

.rel {
  position: relative;
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
}

.hidden {
  display: none;
}


/* +++++++++++++++++++++++++++++++++++++ */

.d-flex {
  display: flex;
}
.jucc {
  justify-content: center;
}
.alic {
  align-items: center;
}
.fdc {
  flex-direction: column;
}
._hover {
  &:hover {
    color: lighten($jungle-green, 25%);
    // text-shadow: 0 0  14px white;
  }
}
/* +++++++++++++++++++++++++++++++++++++ */
@media (prefers-reduced-motion: no-preference) {
  .top-animation {
    animation: wipe-enter 1.5s;
  }
  @keyframes wipe-enter {
    0% {
      transform: translate(0, -100px);
      opacity: 0;
    }
    100% {
      transform: translate(0, 0);
      opacity: 1;
    }
  }

  .left-animation {
    animation: left-enter 1.5s;
  }
  @keyframes left-enter {
    0% {
      transform: translate(-200px, 0);
      opacity: 0;
    }
    100% {
      transform: translate(0, 0);
      opacity: 1;
    }
  }

  .right-animation {
    animation: right-enter 1.5s;
  }
  @keyframes right-enter {
    0% {
      transform: translate(200px, 0);
      opacity: 0;
    }
    100% {
      transform: translate(0, 0);
      opacity: 1;
    }
  }
}
/* +++++++++++++++++++++++++++++++++++++ */

.lock {
  overflow: hidden;
}
.ttu {
  text-transform: uppercase;
}
